<template>
  <div class="edit-business-box">
    <gs-button
      :disabled="disabled"
      type="text-primary"
      @click="showModal"
    >
      编辑
    </gs-button>
    <gs-modal
      v-model="visible"
      :width="500"
      :top="56"
      class="add-business-modal"
      title="编辑业务信息"
      has-form
    >
      <gs-form
        ref="businessForm"
        :model="businessForm"
        :rules="rules"
        label-width="100px"
        style="width: 95%;"
      >
        <gs-form-item
          prop="name"
          label="业务对象名称"
        >
          <gs-input
            v-model="businessForm.name"
            placeholder="请输入业务对象名称"
          />
        </gs-form-item>
        <gs-form-item
          prop="module"
          label="业务模块"
        >
          <gs-input
            v-model="businessForm.module"
            placeholder="请输入业务模块"
          />
        </gs-form-item>
        <gs-form-item
          prop="description"
          label="业务描述"
        >
          <gs-textarea
            v-model="businessForm.description"
            placeholder="请输入描述信息"
          />
        </gs-form-item>
        <gs-form-item
          prop="owner"
          label="所有者"
        >
          <gs-input
            v-model="businessForm.owner"
            placeholder="请输入所有者"
          />
        </gs-form-item>
        <gs-form-item
          prop="manager"
          label="管理者"
        >
          <gs-input
            v-model="businessForm.manager"
            placeholder="请输入管理者"
          />
        </gs-form-item>
        <gs-form-item
          prop="maintainer"
          label="维护者"
        >
          <gs-input
            v-model="businessForm.maintainer"
            placeholder="请输入维护者"
          />
        </gs-form-item>
        <gs-form-item
          prop="dataAssets"
          label="相关数据资产"
        >
          <gs-textarea
            v-model="businessForm.dataAssets"
            placeholder="请填写相关数据资产"
          />
        </gs-form-item>
      </gs-form>
      <template slot="footer">
        <gs-button
          :loading="isUpdating"
          type="primary"
          @click="handleConfirm"
        >
          确定
        </gs-button>
        <gs-button
          :disabled="isUpdating"
          @click="close"
        >
          取消
        </gs-button>
      </template>
    </gs-modal>
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import {
  checkLoading
} from 'src/store/status';

export default {
  props: {
    disabled: {
      type: Boolean,
      default: true
    },
    businessId: {
      type: String,
      default: ''
    },
    businessObj: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },

  data() {
    return {
      visible: false,
      businessForm: {
        name: '',
        module: '',
        description: '',
        owner: '',
        manager: '',
        maintainer: '',
        dataAssets: ''
      },
      rules: {
        name: [{
          required: true,
          trigger: 'blur',
          max: 200,
          message: '请输入业务对象名称，且长度不能超过200字符'
        }],
        module: [{
          required: true,
          trigger: 'blur',
          max: 200,
          message: '请输入业务模块，且长度不能超过200字符'
        }],
        description: [{
          required: true,
          trigger: 'blur',
          max: 200,
          message: '请输入描述信息，且长度不能超过200字符'
        }],
        owner: [{
          required: true,
          trigger: 'blur',
          max: 200,
          message: '请输入所有者，且长度不能超过200字符'
        }],
        manager: [{
          required: true,
          trigger: 'blur',
          max: 200,
          message: '请输入管理者，且长度不能超过200字符'
        }],
        maintainer: [{
          required: true,
          trigger: 'blur',
          max: 200,
          message: '请输入维护者，且长度不能超过200字符'
        }],
        dataAssets: [{
          required: true,
          trigger: 'blur',
          max: 200,
          message: '请填写相关数据资产，且长度不能超过200字符'
        }]
      }
    };
  },

  computed: {
    ...mapState('business', {
      businessUpdateStatus: 'businessUpdateStatus'
    }),
    isUpdating() {
      return checkLoading(this.businessUpdateStatus);
    }
  },

  watch: {
    visible(val) {
      if (val) {
        this.$refs.businessForm.resetFields();
        const {name, module, description = '', owner, manager, maintainer, dataAssets} = this.businessObj;
        this.businessForm = {name, module, description, owner, manager, maintainer, dataAssets};
      }
    }
  },

  methods: {
    ...mapActions('business', [
      'updateBusinessInfo'
    ]),

    showModal() {
      this.visible = true;
    },

    handleConfirm() {
      this.$refs.businessForm.validate(valid => {
        if (valid) {
          this.updateBusinessInfo({
            params: {
              param: this.businessForm,
              id: this.businessId
            },
            isManage: false
          }).then(res => {
            this.$Message.success('修改业务信息成功');
            this.$emit('edit-success');
            this.close();
          });
        }
      });
    },

    close() {
      this.visible = false;
    }
  }
};
</script>

