import {
  mapState,
  mapActions
} from 'vuex';
import SideTree from 'src/components/side-tree';
import {
  checkLoading
} from 'src/store/status';

const actions = (data) => {
  return [
    {
      label: '在前添加同级目录',
      value: 0
    },
    {
      label: '在后添加同级目录',
      value: 1
    },
    {
      label: '添加子目录',
      disabled: data.level >= 2,
      value: 2
    },
    {
      label: '编辑',
      value: 3
    },
    {
      label: '删除',
      value: 4
    }
  ];
};
export default {
  components: {
    SideTree
  },

  data() {
    return {
      isSearchMode: false,
      visible: false,
      actionType: -1,
      editingCategory: {},
      actions,
      activeCategory: {}
    };
  },

  computed: {
    ...mapState('technology', {
      categories: 'categories'
    }),
    isLoadingCategory() {
      return checkLoading(this.categories.status);
    },
    treeProps() {
      const {
        categories
      } = this;
      const data = categories.list;

      return {
        data,
        expandOnFilterMatch: true,
        filterNodeMethod: (val, data) => {
          if (!val) {
            return true;
          }
          return data.name.indexOf(val) !== -1;
        },
        props: {
          label: 'name',
          value: 'categoryGuid'
        }
      };
    }
  },


  methods: {
    ...mapActions('technology', [
      'getTopLevelCategory',
      'deleteCategory'
    ]),
    /**
     * tree搜索处理方法
     * @param {*} value 搜索值
     */
    handleCategorySearch(value) {
      this.$refs.sideTree.$refs.tree.filter(value);
    },
    /**
     * 选中某个tree节点处理方法
     * @param {*} category tree的元素节点
     */
    handleSelectCategory(category) {
      this.isSearchMode = false;
      this.activeCategory = category;
    },
    /**
     * tree的菜单选项点击处理方法
     */
    handleNodeAction(data, type) {
      switch (type) {
        case 0:
          this.editCategory(type, {
            guid: data.categoryGuid,
            direction: 'up'
          });
          break;
        case 1:
          this.editCategory(type, {
            guid: data.categoryGuid,
            direction: 'down'
          });
          break;
        case 2:
          this.editCategory(type, {
            guid: data.categoryGuid,
            parentCategoryGuid: data.categoryGuid
          });
          break;
        case 3:
          this.editCategory(type, {
            name: data.name,
            description: data.description,
            guid: data.categoryGuid
          });
          break;
        case 4:
          this.removeCategory(data);
          break;
      }
    },

    editCategory(type, data = {}) {
      this.actionType = type;
      this.visible = true;
      this.editingCategory = data;
    },

    removeCategory(data) {
      const {
        name,
        categoryGuid
      } = data;

      this.$Modal.confirm({
        title: `确定要删除 ${name} 目录吗？`,
        onOk: () => {
          this.deleteCategory(categoryGuid).then(() => {
            this.$Message.success('删除成功');
            this.afterDeleteCategory && this.afterDeleteCategory(categoryGuid);
          });
        }
      });
    }
  }
};
