<template>
  <div class="edit-tech-box">
    <gs-button
      :disabled="disabled"
      type="text-primary"
      @click="showModal"
    >
      编辑
    </gs-button>
    <gs-modal
      v-model="visible"
      :width="900"
      :top="56"
      class="edit-tech-modal"
      title="编辑技术信息"
    >
      <gs-row>
        <two-column-layout>
          <side-tree
            ref="sideTree"
            slot="left"
            :loading="isLoadingCategory"
            :active="activeCategory"
            :actions="actions"
            :tree-props="treeProps"
            :has-action="false"
            @search="handleCategorySearch"
            @node-click="handleSelectCategory"
            @set-active="setActive"
          />
          <template slot="right">
            <gs-tabs
              v-model="activeTab"
              type="content-tab"
            >
              <gs-tab-pane
                label="可选表"
                name="list"
              >
                <div class="action-group">
                  <gs-button
                    :disabled="!selecteds.length"
                    type="primary"
                    @click="handleMutilAdd"
                  >
                    批量添加
                  </gs-button>
                  <gs-search
                    class="search-table"
                    placeholder="请输入表的名称"
                    @search="handleSearch"
                  />
                </div>

                <data-table
                  ref="modalTable"
                  :loading="isRelationLoading"
                  :table-props="tableProps"
                  :pagination-props="paginationProps"
                  @selection-change="handleSelectionChange"
                  @page-change="handlePageChange"
                  @page-size-change="handlePageSizeChange"
                >
                  <el-table-column
                    :selectable="isSelectable"
                    type="selection"
                    width="55"
                  />
                  <el-table-column
                    prop="tableName"
                    label="数据表"
                    show-overflow-tooltip
                  >
                    <template slot-scope="scope">
                      <span
                        :class="{
                          'table-name': true,
                          'is-deleted': scope.row.status === 'DELETED'
                        }"
                      >
                        <gs-icon name=" iconfont icon-biaoge" />{{ scope.row.tableName }}
                      </span>
                    </template>
                  </el-table-column>
                  <el-table-column
                    prop="dbName"
                    label="所属数据库"
                    show-overflow-tooltip
                  >
                    <template slot-scope="scope">
                      <gs-icon name=" iconfont icon-yuanshuju" />{{ scope.row.dbName }}
                    </template>
                  </el-table-column>
                  <el-table-column
                    prop="guid"
                    label="操作"
                    width="100"
                  >
                    <template slot-scope="scope">
                      <gs-button
                        :disabled="selectedTableIds.indexOf(scope.row.tableGuid) > -1"
                        type="text-primary"
                        @click="handleAdd(scope.row)"
                      >
                        添加
                      </gs-button>
                    </template>
                  </el-table-column>
                </data-table>
              </gs-tab-pane>
              <gs-tab-pane
                label="已选表"
                name="added"
              >
                <el-table
                  :data="selectedTables"
                  :row-class-name="deletedClassName"
                  class="selected-table"
                  max-height="348"
                  @cell-click="handleTrust"
                >
                  <el-table-column
                    prop="trust"
                    label="T"
                    width="120px"
                    class-name="trust-column"
                  >
                    <template slot-scope="scope">
                      <span>
                        <gs-icon
                          v-if="scope.row.tableGuid === trustTable.tableGuid"
                          name="star"
                          title="唯一信任技术数据"
                        />
                        <gs-tag
                          v-if="scope.row.status === 'DELETED'"
                          class="deleted-tag"
                        >已删除</gs-tag>
                      </span>
                    </template>
                  </el-table-column>
                  <el-table-column
                    prop="tableName"
                    label="数据表"
                    show-overflow-tooltip
                  >
                    <template slot-scope="scope">
                      <span
                        class="table-name"
                      >
                        <gs-icon name=" iconfont icon-biaoge" />{{ scope.row.tableName }}
                      </span>
                    </template>
                  </el-table-column>
                  <el-table-column
                    prop="dbName"
                    label="所属数据库"
                    show-overflow-tooltip
                  >
                    <template slot-scope="scope">
                      <gs-icon name=" iconfont icon-yuanshuju" />{{ scope.row.dbName }}
                    </template>
                  </el-table-column>
                  <el-table-column
                    prop="value"
                    label="操作"
                    width="80px"
                  >
                    <template slot-scope="scope">
                      <gs-button
                        type="text-primary"
                        @click="removeTable(scope)"
                      >
                        移除
                      </gs-button>
                    </template>
                  </el-table-column>
                </el-table>
              </gs-tab-pane>
            </gs-tabs>
          </template>
        </two-column-layout>
      </gs-row>
      <template slot="footer">
        <action-comfirm
          v-if="selecteds.length"
          title="勾选所选表请点击批量添加， 直接确定不会保留添加关系，是否继续？"
          @comfirm="handleConfirm"
        >
          <gs-button
            slot="reference"
            :loading="isCreating"
            type="primary"
            class="confirm-btn"
          >
            确定
          </gs-button>
        </action-comfirm>
        <gs-button
          v-if="!selecteds.length"
          slot="reference"
          :loading="isCreating"
          type="primary"
          class="confirm-btn"
          @click="handleConfirm"
        >
          确定
        </gs-button>
        <gs-button
          :disabled="isCreating"
          @click="close"
        >
          取消
        </gs-button>
      </template>
    </gs-modal>
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import {
  checkLoading
} from 'src/store/status';
import categoryMixin from './category-mixin';
import DataTable from 'src/components/data-table';
import TwoColumnLayout from 'src/components/two-column-layout';
import ActionComfirm from 'src/components/action-comfirm';

export default {
  components: {
    DataTable,
    TwoColumnLayout,
    ActionComfirm
  },

  mixins: [categoryMixin],

  props: {
    disabled: {
      type: Boolean,
      default: true
    },
    categoryGuid: {
      type: String,
      default: ''
    },
    techTables: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },

  data() {
    return {
      visible: false,
      keyword: '',
      page: 1,
      pageSize: 5,
      selecteds: [],
      activeTab: 'list',
      selectedTables: [],
      trustTable: {}
    };
  },

  computed: {
    ...mapState('technology', {
      currentRelations: 'currentRelations',
      searchRelations: 'searchRelations',
      relationsStatus: 'relationsStatus',
      techUpdateStatus: 'techUpdateStatus'
    }),
    isCreating() {
      return checkLoading(this.techUpdateStatus);
    },
    selectedTableIds() {
      return this.selectedTables.map(item => item.tableGuid);
    },
    tableData() {
      const {
        currentRelations,
        searchRelations,
        isSearchMode,
        activeCategory
      } = this;
      let data = [];

      if (isSearchMode) {
        data = searchRelations;
      } else {
        data = currentRelations;
      }

      return activeCategory.categoryGuid ? data : {total: 0, list: []};
    },
    tableProps() {
      const {
        tableData
      } = this;

      return {
        total: tableData.total,
        data: tableData.list
      };
    },
    paginationProps() {
      const total = this.tableProps.total || 0;

      return {
        current: this.page,
        pageSize: this.pageSize,
        total,
        layout: 'pager'
      };
    },
    isRelationLoading() {
      return checkLoading(this.relationsStatus);
    }
  },

  watch: {
    activeCategory(category) {
      if (category.categoryGuid) {
        this.activeTab = 'list';
        this.page = 1;
        this.getCategoryRelation();
      }
    },
    selectedTables(data) {
      const {
        trustTable
      } = this;
      if (!data.length) {
        this.trustTable = {};
        return;
      }
      const temp = data.filter(item => {
        return !!item.trust;
      });
      if (temp && temp.length) {
        this.trustTable = temp[0];
        return;
      }
      if (!trustTable.tableGuid) {
        this.trustTable = data[0];
        return;
      }
      const isExsit = data.some(item => {
        return item.tableGuid === trustTable.tableGuid;
      });
      if (!isExsit) {
        this.trustTable = data[0];
      }
    }
  },

  methods: {
    ...mapActions('technology', [
      'getRelations',
      'findRelations',
      'updateTechInfo'
    ]),

    showModal() {
      this.visible = true;
      this.activeTab = 'list';
      this.keyword = '';
      this.selectedTables = JSON.parse(JSON.stringify(this.techTables));
      this.selecteds = [];
      this.$refs.modalTable.clearSelection();
      this.activeCategory = {};
      this.getTopLevelCategory(false);
    },

    setActive(val) {
      if (val && val.categoryGuid) {
        this.activeCategory = val;
      }
    },

    isSelectable(row) {
      return this.selectedTableIds.indexOf(row.tableGuid) === -1;
    },

    /**
     * 初始化默认选中情况
     */
    initSelect(tree, defaultChckedRelations, selectedArr) {
      tree.forEach(item => {
        if (defaultChckedRelations.indexOf(item.value) !== -1) {
          // 组件库disabled不能设置选中，所以先设置为false，选中后，再还原true
          item.disabled = false;
          this.$refs.tree.setChecked(item.value, true);
          item.disabled = true;
        }
        if (selectedArr.indexOf(item.value) !== -1) {
          this.$refs.tree.setChecked(item.value, true);
        }
        if (item.children) {
          this.initSelect(item.children, defaultChckedRelations, selectedArr);
        }
      });
    },

    getCategoryRelation() {
      const {
        activeCategory
      } = this;
      return this.getRelations({
        categoryGuid: activeCategory.categoryGuid,
        isManage: false,
        param: {
          offset: (this.page - 1) * this.pageSize,
          limit: this.pageSize
        }
      });
    },
    /**
     * 查询页面
     */
    query() {
      if (this.isSearchMode) {
        this.findRelations({
          filterTableName: this.keyword,
          isManage: false,
          param: {
            offset: (this.page - 1) * this.pageSize,
            limit: this.pageSize
          }
        });
      } else {
        this.getCategoryRelation();
      }
    },
    handlePageChange(page) {
      this.page = page;
      this.query();
    },

    handlePageSizeChange(pageSize) {
      this.page = 1;
      this.pageSize = pageSize;
      this.query();
    },

    handleSearch(value) {
      this.page = 1;
      this.keyword = value;
      this.isSearchMode = !!value;
      this.query();
    },

    deletedClassName({row, rowIndex}) {
      const isDeleted = row.status === 'DELETED';
      return isDeleted ? 'is-deleted' : '';
    },

    handleTrust(row, column, cell) {
      if (column.property !== 'trust') return;
      let obj = {};
      this.selectedTables.map((item, i) => {
        if (item.tableGuid === row.tableGuid) {
          item.trust = true;
          obj = item;
        }
      });
      this.trustTable = obj;
      // this.$set(this.selectedTables, obj, index);
      // console.log(this.selectedTables);
    },
    /**
     * 关联列表复选框change event
     */
    handleSelectionChange(selecteds) {
      this.selecteds = selecteds;
    },

    handleAdd(row) {
      if (this.selectedTableIds.indexOf(row.tableGuid) > -1) {
        this.$Message.warning('该表已被选中');
        return;
      }
      this.selectedTables.push(row);
      this.$refs.modalTable.$refs.dataTable.toggleRowSelection(row);
      this.$Message.success('添加成功');
    },

    handleMutilAdd() {
      const arr = this.selecteds.filter(item => {
        return this.selectedTableIds.indexOf(item.tableGuid) > -1;
      });
      if (arr.length) {
        this.$Message.warning(`表${arr[0].tableName}已被选中`);
        return;
      }
      this.selectedTables = this.selectedTables.concat(this.selecteds);
      this.$Message.success('添加成功');
      this.$refs.modalTable.clearSelection();
    },

    removeTable(scope) {
      this.$delete(this.selectedTables, scope.$index);
    },

    handleConfirm() {
      const {
        selectedTableIds,
        categoryGuid,
        trustTable
      } = this;
      // if (selectedTableIds.length === 0) {
      //   this.$Message.warning('请选择要关联的表');
      //   return;
      // }
      this.updateTechInfo({
        id: categoryGuid,
        isManage: false,
        param: {
          list: selectedTableIds,
          trust: trustTable.tableGuid
        }
      }).then(() => {
        this.$Message.success('修改技术信息成功');
        this.$emit('edit-success');
        this.close();
      });
    },

    close() {
      this.visible = false;
    }
  }
};
</script>

